import React, { Suspense, lazy, useEffect } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import {
  Card,
  CardBody,
  Form,
  FormGroup,
  ActionGroup,
  Checkbox,
  PageSection,
  Divider,
  Text} from '@patternfly/react-core'

import {
  Button,
  StackItem,
  Stack,
  Title,
  Spinner,
} from '@patternfly/react-core';
import { Main } from '@redhat-cloud-services/frontend-components/Main';
import {
  PageHeader,
  PageHeaderTitle,
} from '@redhat-cloud-services/frontend-components/PageHeader';
import { addNotification } from '@redhat-cloud-services/frontend-components-notifications/redux';
import ExternalLinkSquareAltIcon from '@patternfly/react-icons/dist/esm/icons/external-link-alt-icon';
import QuestionIcon from '@patternfly/react-icons/dist/esm/icons/outlined-question-circle-icon'

const targetAddress = "https://sso.dev1.redhat.com/auth/realms/redhat-external/protocol/openid-connect/twofact";

const SampleComponent = lazy(() =>
  import('../../Components/SampleComponent/sample-component')
);

import './AuthPolicy.scss';

/**
 * A smart component that handles all the api calls and data needed by the dumb components.
 * Smart components are usually classes.
 *
 * https://reactjs.org/docs/components-and-props.html
 * https://medium.com/@thejasonfile/dumb-components-and-smart-components-e7b33a698d43
 */
const AuthPolicy = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    insights?.chrome?.appAction?.('sample-page');
  }, []);
  
  const learnMoreLink = () => {
    window.location.href = "https://access.redhat.com/documentation/en-us/red_hat_customer_portal/1/html/using_two-factor_authentication/con-ciam-2fa-about_two-factor-authentication";
  }

  const handleAlert = () => {
    dispatch(
      addNotification({
        variant: 'success',
        title: 'Notification title',
        description: 'notification description',
      })
    );
  };
  
  const handleButtonClick = function (e) {
    console.log("button clicked");
    fetch(targetAddress)
      .then(response => response.json())
      .then(data => console.log(data));
  }

  return (
    <React.Fragment>
      
      <PageHeader>
        <PageHeaderTitle title="Authentication Factors" className="pf-u-mb-lg" />
        <p> Manage authentication factors for your organization </p>
      </PageHeader>
      <Divider/>
      <PageSection>
        <Form>
          <Title headingLevel="h2" size="xl">Organizational two-factor authentication</Title>
          <Text className="pf-u-w-75">
          Enabling two-factor authentication will require all users in your 
          organization to use their password plus a one-time code to log in to their account. 
          In order to generate one-time codes, 
          all users must be able to add an authenticator application to their mobile device. <Button onClick={learnMoreLink} variant="link" component="span" isInline icon={<ExternalLinkSquareAltIcon />} iconPosition="right">
            Learn more about two-factor authentication
          </Button>
          </Text>
          <FormGroup>
            <Checkbox label="Require two-factor authentication for your organization" id="confirmed" className="twofaCheck"></Checkbox>
            <Button variant="link" component="span" isInline className="questionIcon pf-u-ml-xs" icon={<QuestionIcon/>}></Button>
          </FormGroup>
          <ActionGroup>
            <Button onClick={handleButtonClick}>Save</Button>
            <Button variant="link" isInline className="pf-u-ml-lg">Cancel</Button>
          </ActionGroup>
        </Form>
      </PageSection>

    </React.Fragment>
  );
};

export default withRouter(AuthPolicy);
